export default [
  {
    path: '/',
    meta: {
      title: 'Home',
    },
    component: () => import('./pages/home.vue'),
  },
  {
    path: '/start',
    meta: {
      title: 'Quick Start',
    },
    props: {
      title: 'Quick Start',
      subtitle: 'Installation and usage guide',
      color: 'is-primary',
    },
    component: () => import('./pages/start.vue'),
  },
  {
    path: '/demo',
    meta: {
      title: 'Live Demo',
    },
    props: {
      title: 'VueLayers demo app',
      subtitle: 'An example app built with VueLayers.js',
      color: 'is-primary',
    },
    component: () => import('./pages/demo.vue'),
  },
  {
    path: '/packages',
    redirect: '/packages/map',
  },
  {
    path: '/packages/map',
    meta: {
      title: 'Map',
    },
    props: {
      title: 'Map',
      subtitle: 'The Core component of VueLayers',
      color: 'is-info',
    },
    component: () => import('./pages/packages/map.vue'),
  },
  {
    path: '*',
    meta: {
      title: '404 Not Found',
    },
    props: {
      title: '404 Not Found',
      subtitle: 'There is nothing to do here',
      color: 'is-warning',
    },
    component: () => import('./pages/not-found.vue'),
  },
]



